import React from 'react';
import style from './Menu.module.css';
import {NavLink} from 'react-router-dom';

const Menu = (props) => {
  return (
    <div className = {style.menu}>
       <nav>
        <ul>
          <li><NavLink to = '/Lesson'> </NavLink></li>
        </ul>
        <ul>
          <li><NavLink to = '/AddWordsForm'>  </NavLink></li>
        </ul>
        <ul>
          <li><NavLink to ='/NewLessons'> </NavLink></li>
        </ul>
        <ul>
          <li><NavLink to = '/Settings'></NavLink></li>
        </ul>
      </nav> 
    </div>
  )
}
export default Menu;